@use '../util' as *;
@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');

// ***** RESETS *****
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  // 1 rem = 10px > 10:16 = 0.625â€¬ > 62.5% 
  font-size: 62.5%;

  // PHONE
  @include respond(sm-phone) {

  }
}

body {
  box-sizing: border-box;
  font-family: 'Kumbh Sans', sans-serif;

  height: 100vh;
  background-image: linear-gradient(to right bottom, var(--violet), var(--soft-blue));
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;

  // PHONE
  @include respond(sm-phone) {
    min-height: 76.8rem;
  }
}


a, 
a:visited, 
a:active {
  text-decoration: none;
}
