@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap");
/*
  0 - 600px:       Phone
  600 - 900px:     Tablet portrait
  900 - 1200px:    Tablet landscape
  [1200 - 1800] is where our normal styles apply. > Desktop frst aprouch
  1800px + :       Big desktop
*/
/*
// Primer code-a kada koristim mixin:
h1 {
  @include respond(phone) {
    font-size: 3rem;
  }; 
};
*/
/*
 Example:

h1 {
  font-size: rem(33.536);
}

// In CSS:
h1 {
  font-size: 3.3536rem;
}

*/
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
body {
  box-sizing: border-box;
  font-family: "Kumbh Sans", sans-serif;
  height: 100vh;
  background-image: linear-gradient(to right bottom, var(--violet), var(--soft-blue));
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 23.4375em) {
  body {
    min-height: 76.8rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --text-gray-blue-dark: hsl(238, 29%, 17%);
  --text-gray-blue: hsl(237, 12%, 33%);
  --text-divider: hsl(240, 5%, 91%);
  --white: hsl(0, 0%, 100%);
  --violet: hsl(273, 75%, 66%);
  --soft-blue: hsl(240, 73%, 65%);
  --shadow-blue: hsla(260, 74%, 27%, 0.497);
  --orange: hsl(14, 88%, 65%);
}

.section-faq {
  max-width: 144rem;
  margin: 0 auto;
}
@media (max-width: 23.4375em) {
  .section-faq {
    margin: 14.8rem auto 8.5rem auto;
  }
}

.faq {
  font-size: 1.4rem;
  line-height: 1.4rem;
  color: var(--text-gray-blue);
  font-weight: 400;
  background-color: var(--white);
  border-radius: 2.3rem;
  box-shadow: 0px 50px 50px -20px var(--shadow-blue);
  display: grid;
  grid-template-columns: 46rem 46rem;
  grid-template-rows: 25.4rem 25.4rem;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
@media (max-width: 23.4375em) {
  .faq {
    grid-template-columns: 32.7rem;
    grid-template-rows: 53.5rem;
    overflow: visible;
  }
}
@media (max-width: 23.4375em) {
  .faq__imgs {
    grid-column: 1;
    grid-row: 1;
    align-self: center;
    justify-self: center;
  }
}
.faq__info-container {
  grid-column: 2/3;
  grid-row: 1/span 2;
  align-self: center;
  padding-right: 9.5rem;
  margin-top: 4.3rem;
}
@media (max-width: 23.4375em) {
  .faq__info-container {
    grid-column: 1;
    grid-row: 1;
    align-self: end;
    justify-self: end;
    padding: 0 2.3rem 4.8rem 2.4rem;
  }
}
.faq__heading {
  font-size: 3.2rem;
  line-height: 3.2rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--text-gray-blue-dark);
  margin-top: 6.5rem;
  grid-column: 2/3;
  grid-row: 1/2;
  align-self: start;
}
@media (max-width: 23.4375em) {
  .faq__heading {
    text-align: center;
    margin-top: 11.5rem;
    margin-bottom: 3.8rem;
    grid-column: 1;
  }
}
@media (max-width: 23.4375em) {
  .faq__img {
    width: 23.7rem;
  }
}
.faq__img--woman {
  transform: translate(-18%, -92%);
}
@media (max-width: 23.4375em) {
  .faq__img--woman {
    transform: translate(18%, -223%);
  }
}
.faq__img--pattern {
  width: 90.2rem;
  transform: translate(-57%, 26%);
}
@media (max-width: 23.4375em) {
  .faq__img--pattern {
    width: 24rem;
    transform: translate(17%, -83%);
  }
}
.faq__img--box {
  transform: translate(-48%, -107%);
  position: fixed;
  z-index: 99999;
}
@media (max-width: 23.4375em) {
  .faq__img--box {
    transform: translate(-206%, -104%);
    position: absolute;
    width: 10rem;
  }
}
.faq__info-list {
  display: block;
  list-style: none;
}
.faq__info-list-item {
  cursor: pointer;
  padding-bottom: 1.9rem;
  border-bottom: 1px solid var(--text-divider);
  margin: 1.6rem 0;
}
@keyframes slide-down {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.faq__info-list-item:first-of-type {
  margin-top: 0;
}
.faq .arrow {
  margin-right: 2.42rem;
}
.faq .arrow--down {
  transform: rotate(180deg);
}
@media (max-width: 23.4375em) {
  .faq .arrow {
    margin-right: 0rem;
  }
}
.faq__info-question-box {
  display: flex;
  justify-content: space-between;
}
.faq__info-question-box[aria-expanded=true] + .faq__info-answer {
  display: block;
  animation: slide-down 400ms ease-in;
}
.faq__info-question-box[aria-expanded=true] .faq__info-question {
  font-weight: 700;
  color: #1f2038;
}
@media (max-width: 23.4375em) {
  .faq__info-question-box {
    gap: 2rem;
  }
}
.faq__info-question:hover {
  color: var(--orange);
}
.faq__info-answer {
  margin-top: 1.1rem;
  font-size: 1.2rem;
  line-height: 1.8rem;
  display: none;
}